
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'









export default{
components: {
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Loading
},
data () {
return {
inputmessage: '',  //  
light: false,  //  input
clickmore: false, // 
gropname: '', //  
ifme: false,  //  
enlargeurl: '', //  
enlargehides: false,
enlargeShow: false,
enlarge: false,
timer: null,
groupconversine: [], // 
offset: 0,
imgS: '',
scroll: '',
loadStatus: false, // 
underscore: false, // 
chatData: {},
imgurl: '',
userId: '',
allgroups: [],    // 
testLine: '33',
username: ''

}
},
created () {
},
mounted () {
this.$vux.loading.show({
text: 'loading'
})
this.getUserInfo()
this.$socket.on('userJoined', (msg) => {
this.testLine = msg.username
})
this.$socket.on('newMessage', (msg) => {
if (!msg) {
return
};
console.log('denglule :' + this.username)
console.log(msg.message)
this.groupconversine.push(msg)
})
},
computed: {},
beforeDestroy () {
},
methods: {
enterThing () {

},
enlargeHide () {

},
enlargeImg (enlargeImg) {
this.enlargeurl = enlargeImg
this.enlarge = true
this.enlargeShow = true
this.enlargehides = false
},
loadMore () {
},
inputBottomHide () {
this.clickmore = false
},
whatInput () {
if (this.inputmessage.replace(/\s+/g, '') === '') {
this.light = false
} else {
this.light = true
}
},
bottomShow () {
this.clickmore = true
},
clickSend () {
console.log('inputMessage:' + this.inputmessage)
this.$socket.emit('newMessage', this.inputmessage)
this.inputmessage = ''
this.light = false
this.$nextTick(() => {
window.scrollTo(0, this.$refs.groupHeight.offsetHeight - window.innerHeight)
})
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
this.username = res.data.username
this.$socket.emit('add user', res.data.username)
console.log('denglule1 :' + this.username)
this.$vux.loading.hide()
})
.catch((error) => {
console.log(error)
this.$vux.loading.hide()
})
}
}
}



